.default {
    position: relative;
    display: inline-block;
    font-size: 20px;
    font-weight: 700;
    color: rgb(209, 209, 217);
    text-decoration: none;
    text-shadow: 0 -1px 2px rgba(0, 0, 0, .2);
    padding: .5em 1em;
    outline: none;
    border-radius: 3px;
    background: linear-gradient(rgb(151, 169, 238), rgb(57, 57, 231)) rgb(110, 112, 120);
    box-shadow: 0 1px rgba(255, 255, 255, .2) inset,
    0 3px 5px rgba(0, 1, 6, .5),
    0 0 1px 1px rgba(0, 1, 6, .2);
    transition: .2s ease-in-out;
    /*margin-top: 10px;*/
}

.default:hover:not(:active,:disabled) {
    background: linear-gradient(rgb(126, 126, 134), rgb(70, 71, 76)) rgb(126, 126, 134);
}

.default:active {
    top: 1px;
    background: linear-gradient(rgb(76, 77, 82), rgb(56, 57, 62)) rgb(76, 77, 82);
    box-shadow: 0 0 1px rgba(0, 0, 0, .5) inset,
    0 2px 3px rgba(0, 0, 0, .5) inset,
    0 1px 1px rgba(255, 255, 255, .1);
}


.red {
    background: linear-gradient(rgb(255, 40, 40), rgb(147, 1, 1)) rgb(182, 1, 1);;

}

.default:disabled {
    background: linear-gradient(rgb(202, 202, 202), rgb(126, 126, 126)) rgb(126, 126, 134);
    color: #545454;
}